@import url('https://fonts.googleapis.com/css2?family=Mulish&family=Roboto:ital,wght@0,400;0,500;0,700;1,900&display=swap');

*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Mulish', sans-serif;
}


.navbar{
    background-color: #fff !important;
    box-shadow: rgba(33, 35, 38, 0.1) 0px 10px 10px -10px;
    .navbar-brand{
        font-size: 1.9rem;
        color: #565387;
    }
    a{
        font-size: 1.3rem;
        text-transform: capitalize;
        &:hover{
            color: #3498db !important;
        }
    }
    .menu_active{
        font-weight: bold;
        border-bottom: 1px solid #565387;
    }
    
}

#header{
    width: 100%;
    height: 90vh;
    margin-top: 70px;
    h1{
        margin: 0 0 10px 0;
        font-size: 40px;
        font-weight: 700;
        color: #000;
        .brand-name{
            color: #3498db;
        }
        @media screen and (max-width: 550px) {
            font-size: 35px;
        }
    }
    h2{
        color: #484848;
        font-size: 20px;
    }
    .btn-get-started{
        font-weight: 500;
        font-size: 16px;
        letter-spacing: 1px;
        display: inline-block;
        padding: 10px 30px;
        border-radius: 50px;
        transition: 0.5s;
        color: #3498db;
        border: 2px solid #3498db;
        text-decoration: none;
        &:hover{
            background: #3498db;
            color: #fff;
        }
        @media screen and (max-width: 991px) {
            margin-bottom: 50px;
        }
    }
    .header-img{
        text-align: right;
        img{
            width: 100%;
        }
        @media screen and (max-width: 991px) {
            text-align: center;
            img{
                width: 60%;
            }
        }
        @media screen and (max-width: 692px) {
            img{
                width: 80%;
            }
        }
    }
    .animated{
        animation: up-down 2s ease-in-out infinite alternate-reverse both;
    }
    @-webkit-keyframes up-down{
        0%{
            transform: translateY(10px);
        }
        100%{
            transform: translateY(-10px);
        }
    }
    @keyframes up-down{
        0%{
            transform: translateY(10px);
        }
        100%{
            transform: translateY(-10px);
        }
    }
    @media screen and (max-width: 991px) {
        height: 80vh;
        text-align: center;
    }
}

.my{
    margin-top: 150px;
    margin-bottom: 30px;
}

footer{
    p{
        margin: 0;
        padding: 0;
    }
}